import { Component, OnInit, forwardRef, Input, Output, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'mbaas-select-list',
  templateUrl: './select-list.component.html',
  styleUrls: ['./select-list.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SelectListComponent),
      multi: true
    }
  ],
})
export class SelectListComponent implements ControlValueAccessor {

  // tslint:disable-next-line: no-inferrable-types
  disabled: boolean = false;
  etiqueta: string;
  lista: boolean;

  // tslint:disable-next-line: no-input-rename variable-name
  @Input('value') _value: string;
  @Input() error: boolean;
  // tslint:disable-next-line: no-inferrable-types
  @Input() placeHolder: string = 'Seleciona una opción';
  @Input() datos: Array<any> = [];
  @Output() change = new EventEmitter();
  @Output() focus = new EventEmitter();
  @Output() focusout = new EventEmitter();
  @Input() getLabel: (item) => string = ( item ) => item.label;
  @Input() getValue: (item) => string = ( item ) => item.value;
  onChange: any = () => { };
  onTouched: any = () => { };

  constructor() {
    this.etiqueta = '';
    this.lista = false;
  }

  touch(): void {
    this.lista = true;
    this.onTouched();
  }

  ocutar(): void {
    this.lista = false;
  }

  setValue(item): void {
    this.etiqueta = this.getLabel(item);
    this.value = this.getValue(item);
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = value;
    this.onChange(value);
    this.onTouched();
    this.change.emit(value);
  }

  writeValue(value) {
    const resto = this.datos.filter(item => this.getValue(item) === value);
    if ( resto.length !== 0 ) {
      this.etiqueta = this.getLabel(resto[0]);
    } else {
      this.etiqueta = this.placeHolder;
    }
    this._value = value;
    this.change.emit(resto[0]);
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  sendFocus(): void {
    this.focus.emit();
  }

  sendFocusOut(): void {
    this.focusout.emit();
  }


}
